// 친구마다 결제할 최종 금액 저장
const frdStore = {
    namespaced: true,
    state: {
        frdList: [
            {
                frdname: '빽다방',
                frdprice: 0
            },
            {
                frdname: '스타벅스',
                frdprice: 0
            }
        ]
    },
    getters: { 
        GE_FRD_LIST: state => state.frdList
    },
    mutations: {
        ADD_OR_UPDATE_FRIEND: (state, friend) => {
            const existingFriend = state.frdList.find(f => f.frdname === friend.frdname);
            if (existingFriend) {
                existingFriend.frdprice += friend.frdprice; 
            } else {
                state.frdList.push(friend);
            }
        },
        
    }
}
export default frdStore