const cartStore = {
    namespaced: true,
    state: {
        cartList: [
            {
                //  메뉴명, 메뉴일련번호, 옵션여부, 가격, 수량
                fname: '짜장면',
                fum: '2',
                foption: '없음',
                fprice: 500, // 메뉴 가격
                ftotal:500, // 수량 * 가격
                fcnt: 1
            },
            {
                fname: '짬뽕',
                fnum: '1',
                foption: '곱빼기(+500원)',
                fprice: 5000,
                ftotal:5000,
                fcnt: 1
            }
        ]
    },
    getters: { // 상태 가져오는 함수
        GE_CART_LIST: state => state.cartList
    },
    mutations: { // 상태 변경 함수
        MU_CART_LIST: (state, payload) => { 
            state.cartStore = payload
        },
        INCREMENT_COUNT: (state, index) => {
            state.cartList[index].fcnt++;
            state.cartList[index].ftotal= state.cartList[index].fprice * state.cartList[index].fcnt;
        },
        DECREMENT_COUNT: (state, index) => {
            if (state.cartList[index].fcnt > 1) {
                state.cartList[index].fcnt--;
                state.cartList[index].ftotal -= state.cartList[index].fprice;
            }
        },
        RM_CART_LIST:(state, index) =>{
            state.cartList.splice(index, 1);
        }
    }
}
export default cartStore